.container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
}

.heading{
    font-weight: unset;
}

@media (min-width: 768px){
    .container{
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 1014px){
    .container{
        grid-template-columns: repeat(4, 1fr);
    }
}